@import '../../../styles/settings.scss';

.Home {
  &__Header {
    position: relative;
    &-bg {
      font-size: 55rem;
      color: $color-font-contrast-second;
      opacity: 0.15;
    }
  }
  &__Section {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 99;
    &-content {
      align-self: flex-start;
    }
    &-paragraph {
      font-size: $font-big;
      padding: 1rem 0;
    }
    &-btn {
      margin: 1rem 0 0 -5.5rem;
    }
  }
}
